<template>
  <div class="level">
        <van-nav-bar
            title="选择级别"
            left-text="返回"
            left-arrow
            @click-left="$router.go(-1)"
        />
      <div class="wrap">
          <div class="tag_wrap">
                <div class="scr">
                    <div class="tag" v-if="save.id">
                        <p> {{save.name}} </p>
                        <van-icon name="cross" @click="clear"/>
                    </div>
                </div>
                <div class="clear"  @click="clear">
                    <p>清除</p>
                    <van-icon name="cross" />
                </div>
          </div>
      </div>
      <div class="choose">
            <div class="nav">
                <div class="item" :class=" item.id == save.id ? 'active' : ''"  @click="choose(item)" v-for="item in list" :key="item.id">
                    <img :src="item.url" alt="">
                    <p>{{item.name}}</p>
                </div>
            </div>
      </div>


    <!-- <van-overlay :show="show" @click="show = false">
    <div class="wrapper" @click.stop>
        <div class="box">
            <div class="choose_wrap" v-show="show">
                <div class="item" :class="{'active': item.active}" @click="chooseItem(item, index)" v-for="(item, index) in chooseList" :key="item.id">{{item.name}}</div>
            </div>
            <div class="btn">
                <van-button type="primary" size="large" color="#2873ff" @click="submit">确定</van-button>
            </div>
        </div>
    </div>
    </van-overlay> -->
            
        <div class="btn-wrap">
            <van-button type="primary" size="large" color="#2873ff" @click="ok">确定</van-button>
        </div>
  </div>
</template>
 
<script>
import { mapState, mapGetters } from "vuex";
import api from '../api/index'
export default {
    name: 'level',
    data () {
        return {
            show: false,
            // chooseList: [],
            // saveList: [],
            list: [],
            save: {}
            // saveArr: [],
            // saveWrap: []
        }
    },
    computed: {
        ...mapState({
            s_sex: state => state.s_sex,
            s_level: state => state.s_level
        })
    },
    created() {
        api.tableInfo().then(res => {
            console.log(res.data.data.carLevel)

            this.list = res.data.data.carLevel;
        })
    },
    methods: {
        clear() {
            this.save = {};
        },
        ok() {
            // console.log( this.saveWrap )
            // let str = '';
            // this.saveWrap.forEach((item, index) => {
            //     if(index == 0) {
            //         str += item.name;
            //     } else {
            //         str += ',' + item.name;
            //     }
            // })
            // this.$store.state.s_level.list = this.save;
            this.$store.state.s_level.val = this.save;
            this.$router.back(-1)
        },
        choose(val) {
            console.log(val)
            this.save = val;
            // let arr = [];
            // if(val.children) {
            //     val.children.forEach( item => {
            //         item.active = false;
            //     })
            //     this.chooseList = val.children;
            //     this.show = true;
            // }
        },
        onCancel() {

        },
        chooseItem(val, index) {
            console.log( val, index)
            if(val.active) {
                val.active = false;
                let arr = this.saveList;
                arr.forEach((item, index) => {
                    if(item.id == val.id) {
                        arr.splice(index, 1);
                    }
                })
                // console.log( arr )
            } else {
                val.active = true;
                this.saveList.push(val)
            }
            this.$set(this.chooseList, index, val);
            // console.log( this.chooseList )
            // console.log( this.saveList)
            let saveArr = [];
            this.chooseList.forEach(item => {
                if(item.active == true) {
                    saveArr.push(item);
                }
            })
            this.saveArr = saveArr;
            console.log(saveArr);
        },
        submit() {
            console.log( this.saveArr )
            this.show = false;
            this.saveArr.forEach(item => {
                this.saveWrap.push(item)
            })
        }
    }
}
</script>
<style lang="stylus" scoped>
.level
    .wrap
        .tag_wrap
            position relative
            display flex
            background #F8F8F8
            padding 12px
            height 24px
            .scr
                display flex
                width 88%
                white-space nowrap
                overflow-x scroll
                &::-webkit-scrollbar
                    width 0
                    height 0
                    background transparent
                .tag
                    display flex
                    align-items center
                    background #FFF
                    padding 4px
                    border-radius 2px
                    margin-right 12px
            .clear
                position absolute
                right 12px
                display flex
                align-items center
                top 11px
                padding 4px
                color #2873fe
    .choose
        .nav
            display flex
            justify-content center
            flex-wrap wrap
            .item 
                text-align center
                width 30%
                display inline-block
                img 
                    width 80%
            .active
                color #2873fe
                background rgba(40,115,255,0.1)
    .btn-wrap
        position fixed
        left 0
        bottom 0
        width 100%
    .box
        position absolute
        bottom 0
        width 100%
        .choose_wrap
            display flex
            justify-content flex-start
            flex-wrap wrap
            padding-bottom 12px
            background #FFF
            .item 
                width 20%
                background #F8F8F8
                text-align center
                padding 12px 0
                color #333
                margin-top 12px
                margin-left 12px
            .active
                color #2873fe
                background rgba(40,115,255,0.1)

</style>